.topline {
	display: inline-block;
	text-align: center;
	position: relative;
	color: #fff;
	top: -80px;

	&__love {
		@include Fs;
		font-size: 38px;
		font-weight: 400;
		&:after {
			content: '';
			display: inline-block;
			position: relative;
			top: 4px;
			@include s(heart);
			margin-left: 5px; } }
	&__sign {
		position: relative;
		top: 14px;
		@include K;
		font-size: 77px;
		font-weight: 400;
		margin: 0 -5px; }
	&__name {
		color: #31e4ff;
		@include Fs;
		font-size: 36px;
		font-weight: 400;
		text-decoration: underline; } }


